}

.left {
  position: relative;
  font-size: 1.9em;
  font-weight: bold;
  margin-left: 4vw;

  /* text not selectable */
  -webkit-user-select: none; /* Safari */ /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

.left > span{
  position: absolute;
  height: 100%;
  font-size: 30px;
  color: yellow;
}

.left:hover {
  cursor: pointer;
}

/* Menu */

.right {
  display: flex;
  flex-direction: row;
  margin-right: 4vw;
  font-size: 1.2em;
  z-index: 2;
}

.headerMenu {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: row;
  margin: 0;
}

.headerMenu > li {
  position: relative;
  margin-right: 2vw;
  font-weight: 600;
  font-size: 1.1em;

  /* text not selectable */
  -webkit-user-select: none; /* Safari */ /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

.headerMenu > li:hover {
  cursor: pointer;
  color: yellow;
  /* border-bottom: 4px solid yellow; */
}

.headerMenu > li:hover::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 15px;
  width: 60%;
  margin: 0 0 0 -30%;
  border-bottom: 3px solid yellow;
}

.right .separator{
  font-size: 1.3em;
  margin-right: 2vw;
  transform: translateY(-5px);
  color: yellow;

  /* text not selectable */
  -webkit-user-select: none; /* Safari */ /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

