.right img {
  max-height: 60%;
}

.german {
  margin-right: 1vw;
}

.language {
  display: flex;
  flex-direction: row;
  height: 100%;
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0.1em 0 0 0;
  font-weight: 600;
}

.language > li {
  position: relative;
  display: block;
  height: 100%;

