  -webkit-user-select: none; /* Safari */ /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

.language > li:hover {
  cursor: pointer;
  color: yellow;
}

.language > li:hover::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 15px;
  width: 50%;
  margin: 0 0 0 -25%;
  border-bottom: 3px solid yellow;
}

