      transform: translateY(3em);
    }

    .headerMenu > li:hover::after {
      content: "";
      position: absolute;
      left: 50%;
      bottom: 15px;
      width: 60%;
      margin: 0 0 0 -30%;
      border-bottom: 0;
    }

    .right > .separator {
      display: none;      
    }

    .mobileSeparator {
      display: block;
      text-align: right;
      margin: 1em 2vw 1em 0;
      font-weight: bold;
      transform: translateY(3em);
    }

    .language {
      display: table;
      text-align: right;
      transform: translateY(3em);
    }

    .language > li {
      display: inline;
      height: auto;  
      margin-right: 2vw;    
    }

    .language > li:hover::after {
      content: "";
      position: absolute;
      left: 50%;
      bottom: 15px;
      width: 50%;
      margin: 0 0 0 -25%;
      border-bottom: 0;
    }

    .german {
      margin-right: 3vw;
    }  
    
  } 
